import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import { formatDate, localStoragedecryp, localStorageencryp } from "../../../../components/helpers/function";
import { NotifInfo, ToastNotification } from "../../../../components/helpers/notification";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { postData } from "../../../../config/axios";
import * as moment from "moment-timezone";

const HeadFormLaporanPenjualanSales = lazy(() =>
  import("./HeadFormLaporanPenjualanSales")
);

class FormLaporanPenjualanSales extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanPenjualanSales: [],
      export: false,
      isDetail: false,
      isRekap : false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    // console.log(data.type);
    this.setState({
      isLoading: true,
      isRekap : data.type === "DETAIL" ? true : false
    });
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
      kode_sales: data.kode_sales,
      type: data.type
    };
    
    postData("penjualan/get/reportsales", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            LaporanPenjualanSales: [],
            isLoading: false,
            export : false
           });
        } else {
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          ToastNotification("success", "Laporan Penjualan Sales Tersedia");
          this.setState({
            LaporanPenjualanSales: res.data,
            isLoading: false,
            export : true
         })
        }
      })
      .catch((error) => { this.errorHandling(error) });
  }

  errorHandling(error) {
    this.setState({
      isLoading: false,
      export : false
     });
    NotifInfo('Data Yang Di Cari Tidak Ada')
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Penjualan Sales</li>
        </ol>
        <h1 className="page-header">Laporan Penjualan Sales </h1>
        <Panel>
          <PanelHeader>Laporan Penjualan Sales</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadFormLaporanPenjualanSales
              export={this.state.export}
              isLoading={this.state.isLoading}
              isRekap={this.state.isRekap}
              LaporanPenjualanSales={this.state.LaporanPenjualanSales}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}

export default FormLaporanPenjualanSales;
